@import url("https://fonts.googleapis.com/css?family=Averia+Libre:100,200,300,400,500,600,700");

:root {
    --color-main: rgba(118, 185, 71, 1);
    --font-averia-libre: "Averia Libre", "sans-serif";
}

.menu {
    position: absolute;
    top: 5rem;

    max-width: 100%;
}

.category {
    margin-bottom: 1rem;

    max-width: 100%;
    width: 100%;
    display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: baseline;
}
.category h1 {

    width: 100%;
    text-align: center;
    font-family: var(--font-averia-libre);
    font-size: 2.5rem;
    font-weight: 700;
}

.menuCard {

    margin-left: 2rem;
    margin-right: 2rem;
    margin-bottom: 3rem;

    max-width: 100%;
    width: 21rem;
    height: 15.5rem;
    background-color: white;
    filter: drop-shadow(0rem .2rem .25rem rgba(0, 0, 0, 0.25));
}
.menuCard .img {
    position: absolute;
    top: 0rem; left: 0rem;

    width: 100%;
    height: 10.5rem;

    overflow: hidden;
}
.menuCard .img .image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.menuCard .img .starIcon {
    position: absolute;
    top: 0rem; left: 0rem;
    
    width: 3rem;
    height: 3rem;
}
.menuCard .desc {
    position: absolute;
    top: 10.5rem; left: 0rem;
    
    width: 100%;
    height: 5rem;
}
.menuCard .desc img {
    position: absolute;
    top: .3rem; left: .5rem;

    width: 1.25rem;
    height: 1.25rem;
}
.menuCard .desc h3 {
    position: absolute;
    top: .25rem; left: 2rem;
    margin: 0;

    color: var(--color-main);
    font-family: var(--font-averia-libre);
    font-size: 1rem;
    font-weight: 700;
}
.menuCard .desc h2 {
    position: absolute;
    top: 1.75rem; left: .5rem;
    margin: 0;

    width: calc(100% - 2*.5rem);
    font-family: var(--font-averia-libre);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: black;
    font-size: 1.2rem;
    font-weight: 700;
}
.menuCard .desc h4 {
    position: absolute;
    top: 3.75rem; left: .5rem;
    margin: 0;

    color: black;
    font-family: var(--font-averia-libre);
    font-size: .75rem;
    font-weight: 700;
}

