
.trendingCard {
    position: absolute;
    width: 20.5rem;
    height: 12.5rem;
    z-index: 1;

    box-shadow: 0rem .25rem .5rem rgba(0, 0, 0, 0.3);
    border-radius: 1rem;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.trendingCard .tcBG {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    filter: brightness(.5) saturate(1.5) blur(.5rem);
    user-select: none;
}

.trendingCard .foreground {
    position: absolute;
    width: 20rem;
    height: 12rem;

    border-radius: 1rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
}

.trendingCard .foreground img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;
}

.trendingCard .foreground .name {
    position: absolute;
    bottom: -.6rem;

    width: 95%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    text-align: center;
    font-size: 1.3rem;
    font-weight: 500;
    font-style: italic;
    color: white;
    text-shadow: .05rem .05rem .25rem rgba(0, 0, 0, 0.75);
    user-select: none;
}

.trendingCard .foreground .nameBG {
    position: absolute;
    bottom: -0.017rem;
    right: 0;
    width: 100%;
    height: 2.5rem;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    background-image: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.35) 0%,
        rgba(255, 255, 255, 0.2) 10%,
        rgba(100, 100, 100, 0.15) 100%
    );
    backdrop-filter: brightness(.5) blur(.2rem);
}