:root {
    --tcWidth: 20.5rem;
    --focusZoom: 1.2;
    --tcGap: 1rem;
    --leftmost_recommended: calc((var(--tcWidth) + var(--tcGap))*-4 + var(--tcWidth)/-2 - (var(--tcWidth)/2 * (var(--focusZoom) - 1)));
    --recommendedPos: 0;
    --recommendedPosPrev: 0;
    --recAnimDur: .25s;

    --recIndex: 4;
}

.recommendedSection {
    position: absolute;
    width: 100%;
    margin-left: 50%;
    margin-right: 50%;
}

.recommendedNav {
    position: absolute;
    margin-left: -50%;
    top: 1.5rem;
    width: 100%;
    z-index: 3;
}
.recommendedButtonLeft {
    position: absolute;
    top: 0rem;
    left: 0rem;

    height: 12.5rem;
    width: 10rem;
    background-image: linear-gradient(
        90deg,
        rgba(0, 0, 0, .75),
        rgba(0, 0, 0, 0)
    );
    background-color: transparent;
    border: none;
    cursor: grab;
}
.recommendedButtonLeft div {
    position: absolute;
    left: 0rem;
    top: 0rem;
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.recommendedButtonLeft img {
    position: absolute;
    left: 1rem;
    top: 4rem;
    width: 4rem;
    transform: rotate(180deg);
    user-select: none;
    -webkit-user-drag: none;
}
.recommendedButtonRight {
    position: absolute;
    top: 0rem;
    right: 0rem;

    height: 12.5rem;
    width: 10rem;
    background-image: linear-gradient(
        -90deg,
        rgba(0, 0, 0, .75),
        rgba(0, 0, 0, 0)
    );
    background-color: transparent;
    border: none;
    cursor: grab;
}
.recommendedButtonRight div {
    position: absolute;
    left: 0rem;
    top: 0rem;
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.recommendedButtonRight img {
    position: absolute;
    right: 1rem;
    top: 4rem;
    width: 4rem;
    user-select: none;
    -webkit-user-drag: none;
}

@keyframes scrollRecommendedOdd {
    0% {left: calc(var(--recommendedPosPrev)*1rem);}
    100% {left: calc(var(--recommendedPos)*1rem);}
}@keyframes scrollRecommendedEven {
    0% {left: calc(var(--recommendedPosPrev)*1rem);}
    100% {left: calc(var(--recommendedPos)*1rem);}
}

@keyframes zoomRecommendedRightToLeft {
    0% {scale: 1; left: calc(var(--tcWidth) * (var(--recIndex) - 4) + var(--focusZoom) * var(--tcWidth)/2 + var(--tcGap) * (var(--recIndex) - 4 + 1)); top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}@keyframes zoomRecommendedRightToLeft0 {
    0% {scale: 1; left: calc(var(--tcWidth) * -4 - var(--focusZoom) * var(--tcWidth)/2); top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}@keyframes zoomRecommendedLeftToRight {
    0% {scale: 1; top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}
@keyframes unzoomRecommendedRightToLeft {
    0% {scale: var(--focusZoom); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}@keyframes unzoomRecommendedRightToLeftOverflow {
    0% {scale: var(--focusZoom); left: calc(var(--leftmost_recommended) + (var(--tcWidth) + var(--tcGap)) * 9); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}
@keyframes unzoomRecommendedLeftToRight {
    0% {scale: var(--focusZoom); left: calc(var(--tcWidth) * (var(--recIndex) - 4 - 1) + var(--focusZoom) * var(--tcWidth)/2 - var(--tcGap) * (9 - var(--recIndex) - 1)); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}@keyframes unzoomRecommendedLeftToRightOverflow {
    0% {scale: var(--focusZoom); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}

.recommendedDishes_slideLeftOdd {
    position: relative;
    animation-name: scrollRecommendedOdd;
    animation-duration: var(--recAnimDur);
    animation-timing-function: ease-in-out;
    left: calc(var(--recommendedPos)*1rem);
}
.recommendedDishes_slideLeftEven {
    position: relative;
    animation-name: scrollRecommendedEven;
    animation-duration: var(--recAnimDur);
    animation-timing-function: ease-in-out;
    left: calc(var(--recommendedPos)*1rem);
}
.recommendedDishes_slideRightOdd {
    position: relative;
