:root {
    --tcWidth: 20.5rem;
    --focusZoom: 1.2;
    --tcGap: 1rem;
    --leftmost_trending: calc((var(--tcWidth) + var(--tcGap))*-4 + var(--tcWidth)/-2 - (var(--tcWidth)/2 * (var(--focusZoom) - 1)));
    --trendingPos: 0;
    --trendingPosPrev: 0;
    --trendAnimDur: .25s;

    --trendIndex: 4;
}

.trendingSection {
    position: absolute;
    width: 100%;
    margin-left: 50%;
    margin-right: 50%;
}

.trendingNav {
    position: absolute;
    margin-left: -50%;
    top: 1.5rem;
    width: 100%;
    z-index: 3;
}
.trendingButtonLeft {
    position: absolute;
    top: 0rem;
    left: 0rem;

    height: 12.5rem;
    width: 10rem;
    background-image: linear-gradient(
        90deg,
        rgba(0, 0, 0, .75),
        rgba(0, 0, 0, 0)
    );
    background-color: transparent;
    border: none;
    cursor: grab;
}
.trendingButtonLeft div {
    position: absolute;
    left: 0rem;
    top: 0rem;
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.trendingButtonLeft img {
    position: absolute;
    left: 1rem;
    top: 4rem;
    width: 4rem;
    transform: rotate(180deg);
    user-select: none;
    -webkit-user-drag: none;
}
.trendingButtonRight {
    position: absolute;
    top: 0rem;
    right: 0rem;

    height: 12.5rem;
    width: 10rem;
    background-image: linear-gradient(
        -90deg,
        rgba(0, 0, 0, .75),
        rgba(0, 0, 0, 0)
    );
    background-color: transparent;
    border: none;
    cursor: grab;
}
.trendingButtonRight div {
    position: absolute;
    left: 0rem;
    top: 0rem;
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.trendingButtonRight img {
    position: absolute;
    right: 1rem;
    top: 4rem;
    width: 4rem;
    user-select: none;
    -webkit-user-drag: none;
}

@keyframes scrollTrendingOdd {
    0% {left: calc(var(--trendingPosPrev)*1rem);}
    100% {left: calc(var(--trendingPos)*1rem);}
}@keyframes scrollTrendingEven {
    0% {left: calc(var(--trendingPosPrev)*1rem);}
    100% {left: calc(var(--trendingPos)*1rem);}
}

@keyframes zoomTrendingRightToLeft {
    0% {scale: 1; left: calc(var(--tcWidth) * (var(--trendIndex) - 4) + var(--focusZoom) * var(--tcWidth)/2 + var(--tcGap) * (var(--trendIndex) - 4 + 1)); top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}@keyframes zoomTrendingRightToLeft0 {
    0% {scale: 1; left: calc(var(--tcWidth) * -4 - var(--focusZoom) * var(--tcWidth)/2); top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}@keyframes zoomTrendingLeftToRight {
    0% {scale: 1; top: 1.5rem;}
    100% {scale: var(--focusZoom);}
}
@keyframes unzoomTrendingRightToLeft {
    0% {scale: var(--focusZoom); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}@keyframes unzoomTrendingRightToLeftOverflow {
    0% {scale: var(--focusZoom); left: calc(var(--leftmost_trending) + (var(--tcWidth) + var(--tcGap)) * 9); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}
@keyframes unzoomTrendingLeftToRight {
    0% {scale: var(--focusZoom); left: calc(var(--tcWidth) * (var(--trendIndex) - 4 - 1) + var(--focusZoom) * var(--tcWidth)/2 - var(--tcGap) * (9 - var(--trendIndex) - 1)); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}@keyframes unzoomTrendingLeftToRightOverflow {
    0% {scale: var(--focusZoom); top: 0rem;}
    100% {scale: 1; top: 1.5rem;}
}

.trendingDishes_slideLeftOdd {
    position: relative;
    animation-name: scrollTrendingOdd;
    animation-duration: var(--trendAnimDur);
    animation-timing-function: ease-in-out;
    left: calc(var(--trendingPos)*1rem);
}
.trendingDishes_slideLeftEven {
    position: relative;
    animation-name: scrollTrendingEven;
    animation-duration: var(--trendAnimDur);
    animation-timing-function: ease-in-out;
    left: calc(var(--trendingPos)*1rem);
}
.trendingDishes_slideRightOdd {
