

.upcDel {
	position: relative;
	width: calc(100% - 2*2rem);
	top: 5rem;
	left: 0rem;
}

.upcDel .list {
	position: absolute;
	display: flex;
	width: 100%;
}
.upcDel .list .viewAll {
	position: relative;

	height: 8rem;
	width: 6rem;
	border-bottom-right-radius: 1rem;
	border-top-right-radius: 1rem;
	background-image: linear-gradient(
		-90deg,
		rgba(0, 0, 0, 0.5) 0%,
		rgba(0, 0, 0, 0.1) 80%,
		rgba(0, 0, 0, 0) 100%
	);
	display: flex;
	align-items: center;
}
.upcDel h2 {
	position: absolute;
	margin: 0%;
	right: 0rem;
	text-align: center;
	font-size: 1.5rem;
	font-weight: 600;
	color: rgb(255, 255, 255);
	text-shadow: 0 0 2rem black;
}