
:root {
    --navBarHeight: 3.5rem;
    --color-main: rgba(118, 185, 71, 1);
    --color-main-50: rgba(118, 185, 71, .5);
    --color-kelly: rgb(47, 82, 51);
    --color-kelly-50: rgb(47, 82, 51, .5);
	--color-spearmint: rgb(148, 201, 115);
	--color-spearmint-50: rgba(148, 201, 115, 0.5);
    --color-x: rgb(40, 82, 52);
    --color-x-50: rgba(40, 82, 52, 0.5);
}

.signUp .bg {
	position: absolute;
	top: var(--navBarHeight); left: 0;

	width: 100%;
	height: calc(100% - var(--navBarHeight));
}
.signUp .bg img {
	position: absolute;
	top: 0; left: 0;
	
	width: 100%;
	height: 100%;
	object-fit: cover;

	filter: contrast(.75) saturate(1.1) brightness(.75);
}

.signUp .form {
	position: absolute;
	top: var(--navBarHeight); left: 0;
	width: 100%;

	display: flex;
	justify-content: center;
}
.signUp .form .white {
	position: absolute;
	max-width: 100%;
	width: 46rem;
	height: 36rem;
	
	border-bottom-left-radius: 2rem;
	border-bottom-right-radius: 2rem;
	background-color: rgba(255, 255, 255, 0.85);
	backdrop-filter: blur(.5rem);
}
.signUp .form h1 {
	position: absolute;
	top: 3.5rem;
	margin: 0;

	text-align: center;
	font-size: 2.5rem;
	font-weight: 700;
}
.signUp .form .errMsg {
	position: absolute;
	top: 6.8rem;
	margin: 0;

	min-width: 40rem;
	text-align: center;
	font-size: 1rem;
	font-weight: 500;
	color: red;
}
.signUp .form .input {
	position: absolute;
	top: 9rem;
	
	max-width: 90%;
	width: 30rem;
	height: 20rem;
}
.signUp .form .input div {
	position: relative;
	margin-bottom: 1.25rem;

	width: 100%;
	height: 3.2rem;

	/* border: solid .1rem rgba(0, 0, 0, 0.2);
	box-sizing: border-box; */
	
    box-shadow: inset 0 0 .2rem rgba(0, 0, 0, 0.5);
	border-radius: 1rem;
	background-color: white;

	display: flex;
	align-items: center;
}
.signUp .form .input div img {
	position: absolute;
	left: 1rem;

	width: 2.1rem;
	height: 2.1rem;

	filter: opacity(.6);
}
.signUp .form .input div input {
	position: absolute;
	left: 3.75rem;

	width: calc(100% - 4rem - 2rem);
	height: 90%;
	border: none;
	outline: none;
	background-color: transparent;

	font-size: 1.2rem;
	font-weight: 500;
}
.signUp .form .input button {
	position: relative;
	margin-top: 2.5rem;

	width: 100%;
	height: 3.5rem;

	border: none;
	border-radius: 5rem;
	background-color: var(--color-main);

	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}
.signUp .form .input button h2 {
	text-align: center;
	font-size: 1.75rem;
	font-weight: 700;
	color: white;
}
.signUp .form .input a:link {
	position: absolute;
	top: 23.25rem; right: .5rem;
	color: black;
	font-size: 1rem;
	font-weight: 600;
}
.signUp .form .input a:visited {
	position: absolute;
	top: 23.25rem; right: .5rem;
	color: black;
	font-size: 1rem;
	font-weight: 600;
}

