@import url("https://fonts.googleapis.com/css?family=Averia+Libre:100,200,300,400,500,600,700");
@import url("https://fonts.googleapis.com/css?family=Lobster+Two:100,200,300,400,500,600,700");

* {
    -webkit-user-drag: none;
    user-select: none;
}

:root {
    --font-averia-libre: "Averia Libre", sans-serif;
    --font-lobster-two: "Lobster Two", sans-serif;

    --navBarHeight: 3.5rem;
    --lightGray: rgb(125, 125, 125);
    --darkGray: rgb(75, 75, 75);
    --lightGrayTranslucent: rgba(125, 125, 125, .85);
    --hoverBG: rgb(240, 240, 240);
    --bRadius: .3rem;

    --color-main: rgba(118, 185, 71, 1);
    --color-main-80: rgba(118, 185, 71, 0.8);

    --right-offset: 2rem;
    --out: -20rem;
    --dur-profile: .15s;
    --dur-burger: .1s;

    --burgerExpandSize: 16rem;
}


/*#region NavBar*/
.navigationBar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;

    width: 100%;
    height: var(--navBarHeight);
    background-color: rgba(250, 250, 250, 0.95);
    backdrop-filter: blur(.5rem);
    box-shadow: 0 0 .5rem rgba(0, 0, 0, .25);
}

.navigationBar .left {
    position: absolute;
    top: 0; left: 0;
    min-width: 15rem;
    width: 15rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: left;
}

.navigationBar .burgerMenu {
    position: relative;
    margin-left: 2rem;
	margin-right: 2rem;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
.navigationBar .burgerMenu img {
    position: absolute;
    width: 100%;
    height: 100%;
}

.navigationBar ._menu {
    position: relative;
    min-width: 6rem;
    width: 6rem;

    text-decoration: none;
    display: flex;
    justify-content: left;
    align-items: center;
}
.navigationBar ._menu h1 {
    font-family: var(--font-averia-libre);
    font-size: 1.2rem;
    font-weight: 600;
    color: black;
}


.navigationBar .right {
    position: absolute;
    top: 0; right: 0;
    min-width: 15rem;
    width: 15rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: right;
}

.navigationBar .pfp {
    position: relative;
    margin-left: 2rem;
    margin-right: 2rem;

    outline: .1rem solid var(--lightGray);
    border-radius: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
    overflow: hidden;
    cursor: pointer;
}
.navigationBar .pfp img {
    width: 100%;
    height: 100%;
    
    object-fit: cover;
}
.navigationBar .preferences {
    position: relative;
    min-width: 6rem;
    width: 6rem;

    text-decoration: none;
    display: flex;
    justify-content: right;
    align-items: center;
}
.navigationBar .preferences h1 {
    font-family: var(--font-averia-libre);
    font-size: 1.2rem;
    font-weight: 600;
    color: black;
}

.navigationBar .middle {
    position: absolute;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.navigationBar .middle .logo {
    position: absolute;
    min-width: 9.5rem;
    width: 9.5rem;
    height: 100%;
    text-decoration: none;

    display: flex;
    align-items: center;
}
.navigationBar .middle .logo img {
    position: absolute;
    left: 0rem;

    width: 3.5rem;
    height: calc(var(--navBarHeight) - 1rem);
    object-fit: contain;
}
.navigationBar .middle .logo h1 {
    position: absolute;
    left: 3.5rem;

    color: black;
    font-size: 1.5rem;
    font-family: var(--font-lobster-two);
    font-weight: 700;
}
/*#endregion*/

/*#region ProfilePopUp*/
@keyframes slideIn_profile {
    0% {right: var(--out);}
    100% {right: 1.5rem;}
}
@keyframes slideOut_profile {
    0% {right: 1.5rem;}
    100% {right: var(--out);}
}

.popUpProfile_out {
    position: fixed;
    top: calc(var(--navBarHeight) + 1.5rem);
    right: var(--out);
    z-index: 3;
}
.popUpProfile_slideIn {
    position: fixed;
    top: calc(var(--navBarHeight) + 1.5rem);
    right: 1.5rem;
    z-index: 3;

    animation-name: slideIn_profile;
    animation-duration: var(--dur-profile);
    animation-timing-function: ease-in-out;
}
.popUpProfile_slideOut {
    position: fixed;
    top: calc(var(--navBarHeight) + 1.5rem);
    right: var(--out);
    z-index: 3;

    animation-name: slideOut_profile;
    animation-duration: var(--dur-profile);
    animation-timing-function: ease-in-out;
}


.profilePopUp {
    position: absolute;
    top: 0;
    right: 0;

    
    outline: .125rem solid var(--lightGrayTranslucent);
    border-radius: 1rem;
    width: 18rem;
    height: 16.75rem;
    background-image: linear-gradient(
        45deg,
        rgba(255, 255, 255, 0.85) 0%,
        rgba(222, 222, 222, 0.85) 100%
    );
    backdrop-filter: blur(.7rem);
}
.profilePopUp .profile {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2.5%;
    margin-bottom: 2.5%;

    border-radius: .9rem;
    height: 5.5rem;
    width: 95%;

    box-shadow: inset .1rem .1rem .1rem rgba(0, 0, 0, 0.25);
    background-image: linear-gradient(
        65deg,
        /* rgba(156, 167, 173, 0.5) 0%,
        rgba(100, 100, 100, 0.5) 100% */
        
        rgba(150, 188, 200, 0.5) 0%,
        rgba(114, 88, 177, 0.5) 100%
    );
    display: flex;
}
.profilePopUp .profile .pfp {
    position: relative;
    align-self: center;
    margin-left: .6rem;

    outline: .1rem solid var(--lightGray);
    border-radius: .8rem;
    height: 80%;
    aspect-ratio: 1/1;
    overflow: hidden;
    cursor: pointer;
}
.profilePopUp .profile .pfp .profilePic {
    position: absolute;
    width: 100%;
    height: 100%;
    
    object-fit: cover;
}
.profilePopUp .profile .pfp .camIcon {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    backdrop-filter: blur(.5rem) brightness(.75);
}
.profilePopUp .profile .pfp .camIcon img {
    position: absolute;
    width: 60%;
    height: 57%;

    filter: contrast(.1) brightness(2);
}

.profilePopUp .profile .data {
    position: relative;
    margin-left: .6rem;
    width: 70%;
    height: 100%;
}
.profilePopUp .profile .data h1 {
    position: relative;
    margin-left: 0;
    margin-top: .75rem;
    height: fit-content;
    width: 70%;

    color: white;
    text-shadow: 0 0 .15rem black;
    font-size: 1.1rem;
    font-weight: 700;
}
.profilePopUp .profile .data ul {
    position: relative;
    margin-top: -.6rem;
    padding-left: 0;
    height: fit-content;
    
    color: white;
    text-shadow: 0 0 .15rem black;
    font-size: .65rem;
    font-weight: 600;
    list-style: none;
    display: table;
}
li { display: table-row;}
b {
    display: table-cell;
    padding-top: .075rem;
    padding-right: .4rem;
    font-size: .65rem;
    font-weight: 600;
}
.profilePopUp .profile .editIcon {
    position: absolute;
    bottom: .5rem;
    right: .5rem;
    width: 1.2rem;

    filter: contrast(.1) brightness(5);
    cursor: pointer;
}
.profilePopUp .aps {
    position: relative;
    margin-left: 2.5%;
    margin-top: 2.5%;
    height: 3rem;
    width: 95%;

    box-shadow: inset .1rem .1rem .1rem rgba(0, 0, 0, 0.25);
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: .9rem;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.profilePopUp .aps .icon {
    position: relative;
    margin-left: 3.25%;
    height: 2rem;
    width: 2rem;
}
.profilePopUp .aps .icon img {
    position: absolute;
    width: 100%;
    object-fit: cover;
}
.profilePopUp .aps h1 {
    position: relative;
    margin-left: 3.25%;
    font-size: 1rem;
    font-weight: 500;
    color: var(--lightGray);
}
/*#endregion*/

/*#region BurgerPopUp*/
@keyframes slideIn_burger {
    0% {left: calc(0rem - var(--burgerExpandSize));}
    100% {left: 0rem;}
}
@keyframes slideOut_burger {
    0% {left: 0rem;}
    100% {left: calc(0rem - var(--burgerExpandSize));}
}

.popUpBurger_out {
    position: fixed;
    top: var(--navBarHeight);
    left: calc(0rem - var(--burgerExpandSize));
    z-index: 3;
    height: 100%;
}
.popUpBurger_slideIn {
    position: fixed;
    top: var(--navBarHeight);
    left: 0rem;
    z-index: 3;
    height: 100%;

    animation-name: slideIn_burger;
    animation-duration: var(--dur-burger);
    animation-timing-function: ease-in-out;
}
.popUpBurger_slideOut {
    position: fixed;
    top: var(--navBarHeight);
    left: calc(0rem - var(--burgerExpandSize));
    z-index: 3;
    height: 100%;

    animation-name: slideOut_burger;
    animation-duration: var(--dur-burger);
    animation-timing-function: ease-in-out;
}


.burgerPopUp {
    position: absolute;
    top: 0; left: 0;
    
    width: var(--burgerExpandSize);
    height: 100%;
    background-color: rgba(255, 255, 255, .95);
    backdrop-filter: blur(1rem);

    display: flex;
    justify-content: center;
}
.burgerPopUp img {
    position: absolute;
    top: 2rem;
    
    width: 8rem;
    height: 8rem;
    object-fit: contain;
}
.burgerPopUp h1 {
    position: absolute;
    top: 8rem;
    
    color: black;
    font-family: var(--font-lobster-two);
    font-size: 2.5rem;
    font-weight: 700;
}
.burgerPopUp .links {
    position: absolute;
    top: 15rem;

    width: 100%;
    height: 30rem;
}
.burgerPopUp .links div {
    position: relative;
    
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: center;
}
.burgerPopUp .links a:link {
    position: absolute;
    
    font-family: var(--font-averia-libre);
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--color-main);
    text-decoration: none;
}
.burgerPopUp .links a:visited {
    position: absolute;
    
    font-family: var(--font-averia-libre);
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--color-main);
    text-decoration: none;
}
/*#endregion*/