



:root {
    --navBarHeight: 3.5rem;
    --color-main: rgba(118, 185, 71, 1);
    --color-main-50: rgba(118, 185, 71, .5);
    --color-kelly: rgb(47, 82, 51);
    --color-kelly-50: rgb(47, 82, 51, .5);
	--color-spearmint: rgb(148, 201, 115);
	--color-spearmint-50: rgba(148, 201, 115, 0.5);
    --color-x: rgb(40, 82, 52);
    --color-x-50: rgba(40, 82, 52, 0.5);
}

.recipe .bg {
	position: fixed;
	top: var(--navBarHeight);
	width: 100%;
	height: calc(100% - var(--navBarHeight));
    overflow: hidden;
    z-index: -4;
}
.recipe .bg div {
	position: fixed;
	width: 100%;
	height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;

	background-color: black;
}
.recipe .bg img {
	position: fixed;
	width: 100%;
	height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;
	filter: opacity(.25) saturate(1) brightness(1);
}

/*#region Top*/
.recipe .top {
	position: absolute;
	top: var(--navBarHeight);
	left: 0rem;

	width: 100%;
	height: 25.5rem;
	background-color: var(--color-kelly-50);
	backdrop-filter: blur(.75rem);

	display: flex;
}
.recipe .text {
	position: relative;
	top: 0rem;
	margin-left: 2rem;

	max-width: 100%;
	min-width: 26rem;
	width: 45rem;
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
}
.recipe .text h1 {
	position: relative;
	margin: 0;
	margin-top: 0.5rem;
	margin-bottom: 1.5rem;

	text-align: center;
	font-size: 3rem;
	font-weight: 700;
	color: white;
}
.recipe .text div {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.recipe .text h2 {
	margin: 0;
	margin-bottom: .35rem;
	color: white;
	text-align: center;
	font-size: 1.5rem;
	font-weight: 600;
}
.recipe .text h3 {
	margin: 0;
	margin-bottom: .75rem;
	color: white;
	text-align: center;
	font-size: 1.25rem;
	font-weight: 500;
}
.recipe ._image {
	position: relative;
	
	width: 48%;
	height: 100%;
}
.recipe ._image .image {
	position: absolute;
	top: 2.35rem;
	left: 2rem;
	width: 75%;
	height: 21rem;
	border-radius: 1rem;
    overflow: hidden;
}
.recipe .image img {
	position: absolute;
	width: 100%;
	height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;
}
.recipe .image .tutorial {
	position: absolute;
	bottom: 0rem;
	left: 0rem;

	width: 65%;
	height: 4rem;
	background-color: rgba(45, 45, 45, 0.5);
	backdrop-filter: blur(.5rem);

	overflow: hidden;
	display: flex;
	align-items: center;
}
.recipe .image .tutorial h2 {
	position: absolute;
	left: 5rem;

	width: 16rem;
	color: white;
	font-size: 1.75rem;
	font-weight: 600;
}
.recipe .image .tutorial img {
	position: absolute;
	left: 2rem;

	width: 2.5rem;
	height: 2.5rem;
}
.recipe .image .order {
	position: absolute;
	bottom: 0rem;
	right: 0rem;

	width: 35%;
	height: 4rem;
	background-color: rgba(255, 255, 255, 0.5);
	backdrop-filter: blur(.5rem);

	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}
.recipe .image .order h2 {
	position: absolute;
	margin-left: -.4rem;

	width: 10rem;
	text-align: center;
	color: rgb(0, 0, 0);
	font-size: 1.75rem;
	font-weight: 700;
}
/*#endregion*/

/*#region Below*/
.below {
	position: absolute;
	top: 29rem;
	left: 0rem;

	width: 100%;

	display: flex;
	flex-direction: column;
}

.ingredients_nutrients {
	position: relative;
	margin-bottom: 3rem;

	width: 100%;

	display: flex;
}
.ingredients {
	position: relative;
	margin-top: 3rem;
	margin-left: 3rem;

	min-width: 24.5rem;
	width: 45%;
	min-height: 20rem;
	border-radius: 1rem;
	background-color: var(--color-spearmint-50);
	backdrop-filter: blur(.75rem);

	display: inline-block;
}
.ingredients h1 {
	position: relative;
	margin: 0;
	margin-top: 2rem;
	margin-left: 2rem;

	color: white;
	font-size: 3rem;
	font-weight: 700;
}
.ingredients ul {
	position: relative;
	margin: 0;
	margin-top: 1.5rem;
	margin-left: 1rem;
	margin-bottom: 2rem;
	margin-right: 1rem;
}
.ingredients li {
	margin-bottom: .5rem;
	color: white;
	font-size: 1.5rem;
	font-weight: 500;
	display: list-item;
}
.nutrients {
	position: relative;
	margin-top: 3rem;
	
	width: 55%;
	height: 20rem;
}
.nutrients h1 {
	position: absolute;
	top: 2rem;
	left: 4rem;
	margin: 0;

	color: white;
	font-size: 3rem;
	font-weight: 700;
	width: 40rem;
}
.nutrients .values {
	position: absolute;
	left: 6.5rem;
}
.nutrients h2 {
	position: absolute;
	left: 4rem;
	margin: 0;

	color: white;
	font-size: 1.5rem;
	font-weight: 500;
	width: 40rem;
}
.nutrients .calories {
	top: 7rem;
}
.nutrients .sugar {
	top: 9.25rem;
}
.nutrients .carbohydrate {
	top: 11.5rem;
}
.nutrients .fat {
	top: 13.75rem;
}
.nutrients .protein {
	top: 16rem;
}
.nutrients .fibers {
	top: 18.25rem;
}

.directions {
	position: relative;
	margin-bottom: 3rem;

	width: 100%;

	display: flex;
	justify-content: right;
}
.directions .aaa {
	position: relative;
	margin-right: 3rem;

	max-width: calc(100% - 6rem);
	width: 60rem;
	min-height: 20rem;
	border-radius: 1rem;
	background-color: var(--color-x-50);
	backdrop-filter: blur(.75rem);

	display: inline-block;
}
.directions h1 {
	position: relative;
	margin: 0;
	margin-top: 2rem;
	margin-left: 2rem;

	color: white;
	font-size: 3rem;
	font-weight: 700;
	width: 40rem;
}
.directions .bbb {
	position: relative;
	margin: 0;
	margin-top: 1.5rem;
	margin-bottom: 1.5rem;
	margin-right: 1.5rem;
	margin-left: 2rem;
}
.directions .ccc {
	margin: 0;
	color: white;
}
.directions h2 {
	margin: 0;
	margin-bottom: .5rem;
	font-size: 1.5rem;
	font-weight: 600;
}
.directions h3 {
	margin: 0;
	margin-bottom: 1.5rem;
	font-size: 1.25rem;
	font-weight: 500;
}
/*#endregion*/

/*#region OrderPopUp*/
@keyframes slideIn_order {
    0% {top: -30rem;}
    100% {top: var(--navBarHeight);}
}
@keyframes slideOut_order {
    0% {top: var(--navBarHeight);}
    100% {top: -30rem;}
}

.popUpOrder_out {
    position: fixed;
    top: -30rem;
	width: 100%;

	display: flex;
	justify-content: center;
    z-index: 3;
}
.popUpOrder_slideIn {
    position: fixed;
    top: var(--navBarHeight);
	width: 100%;

	display: flex;
	justify-content: center;
    z-index: 3;

    animation-name: slideIn_order;
    animation-duration: .2s;
    animation-timing-function: ease-in-out;
}
.popUpOrder_slideOut {
    position: fixed;
    top: -30rem;
	width: 100%;

	display: flex;
	justify-content: center;
    z-index: 3;

    animation-name: slideOut_order;
    animation-duration: .2s;
    animation-timing-function: ease-in-out;
}

.orderPopUp {
    position: absolute;
    top: 0;

    width: 24rem;
    height: 30rem;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
	
    box-shadow: inset 0 0 3rem rgba(0, 0, 0, .1);
    background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, .98) 20%,
        rgba(255, 255, 255, 0.9) 100%
    );
    backdrop-filter: blur(1.5rem);
}
.orderPopUp h1 {
	position: absolute;
	top: 1.5rem; left: 0rem;
	margin: 0;

	width: 100%;
	color: black;
	text-align: center;
	font-size: 2.5rem;
	font-weight: 700;
}
.orderPopUp h2 {
	margin: 0;

	color: black;
	font-size: 1.5rem;
	font-weight: 600;
}
.orderPopUp .amount {
	position: absolute;
	top: 6rem;
	left: 2rem;
}
.orderPopUp .amount h2 {
	position: absolute;
	top: 0rem; left: 0rem;
}
.orderPopUp .amount h3 {
	position: absolute;
	top: 2.22rem; left: 1.5rem;
	margin: 0;

	width: 3rem;
	text-align: center;
}
.orderPopUp .amount button {
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 1.5rem;
	height: 1.5rem;
	border-radius: .25rem;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}
.orderPopUp .amount button h5 {
	margin: 0;

	text-align: center;
	font-size: 1.5rem;
	font-weight: 600;
}
.orderPopUp .amount .min {
	position: absolute;
	top: 2.2rem; left: 0rem;
}
.orderPopUp .amount .min h5 {
	margin-bottom: .2rem;
	font-size: 1.75rem;
}
.orderPopUp .amount .plus {
	position: absolute;
	top: 2.2rem; left: 4.5rem;
}
.orderPopUp .delDate {
	position: absolute;
	top: 11rem;
	left: 2rem;
}
.orderPopUp .delDate .dd {
	position: absolute;
	top: 2.2rem; left: 0rem;
	
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 3.5rem;
	height: 2rem;
	font-weight: 600;
	border-radius: .25rem;
}
.orderPopUp .delDate .mm {
	position: absolute;
	top: 2.2rem; left: 4rem;
	
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 3.5rem;
	height: 2rem;
	font-weight: 600;
	border-radius: .25rem;
}
.orderPopUp .delDate .yyyy {
	position: absolute;
	top: 2.2rem; left: 8rem;
	
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 4.5rem;
	height: 2rem;
	font-weight: 600;
	border-radius: .25rem;
}
.orderPopUp .delTime {
	position: absolute;
	top: 11rem;
	right: 2rem;
}
.orderPopUp .delTime h2 {
	position: absolute;
	top: 0rem;
	right: 1.6rem;
}
.orderPopUp .delTime .hour {
	position: absolute;
	top: 2.2rem; right: 3rem;
	
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 2.5rem;
	height: 2rem;
	font-weight: 600;
	border-radius: .25rem;
}
.orderPopUp .delTime .min {
	position: absolute;
	top: 2.2rem; right: 0rem;
	
	background-color: rgba(0, 0, 0, 0.1);
	border: none;
	padding: 0;

	width: 2.5rem;
	height: 2rem;
	font-weight: 600;
	border-radius: .25rem;
}
.orderPopUp .payment {
	position: absolute;
	top: 16.5rem; left: 2rem;
	width: calc(100% - 2*2rem);
}
.orderPopUp .payment .left {
	position: absolute;
	top: 2.2rem; left: 0rem;
}
.orderPopUp .payment .left h3 {
	margin: 0;
	margin-bottom: .3rem;
	font-size: 1rem;
	font-weight: 600;
}
.orderPopUp .payment .right {
	position: absolute;
	top: 2.2rem; right: 0rem;
}
.orderPopUp .payment .right h3 {
	margin: 0;
	margin-bottom: .3rem;
	text-align: right;
	font-size: 1rem;
	font-weight: 600;
}
.orderPopUp .payment .total {
	position: absolute;
	top: 5.5rem; right: 0rem;

	display: flex;
	flex-direction: column;
	align-items: end;
}
.orderPopUp .payment .total div {
	margin: 0;
	margin-bottom: .5rem;
	
	width: 5rem;
	height: .1rem;
	background-color: black;
}
.orderPopUp .payment .total h3 {
	margin: 0;
	text-align: right;
	font-size: 1rem;
	font-weight: 600;
}
.orderPopUp .cancel {
	position: absolute;
	bottom: 2rem; left: 2rem;

	width: 9.5rem;
	height: 3rem;
	border-radius: .5rem;
	border: none;
	background-color: rgba(0, 0, 0, 0.1);
	
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}
.orderPopUp .cancel h4 {
	color: black;
	font-size: 1rem;
	font-weight: 600;
}
.orderPopUp .placeOrder {
	position: absolute;
	bottom: 2rem; right: 2rem;

	width: 9.5rem;
	height: 3rem;
	border-radius: .5rem;
	border: none;
	background-color: var(--color-main);

	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	cursor: pointer;
}
.orderPopUp .placeOrder h4 {
	color: black;
	font-size: 1rem;
	font-weight: 600;
}


