
:root {
    --navBarHeight: 3.5rem;
    --color-main: rgba(118, 185, 71, 1);
    --color-main-50: rgba(118, 185, 71, .5);
    --color-kelly: rgb(47, 82, 51);
    --color-kelly-50: rgb(47, 82, 51, .5);
	--color-spearmint: rgb(148, 201, 115);
	--color-spearmint-50: rgba(148, 201, 115, 0.5);
    --color-x: rgb(40, 82, 52);
    --color-x-50: rgba(40, 82, 52, 0.5);
}

.editPreferences .bg {
	position: fixed;
	top: var(--navBarHeight); left: 0;

	width: 100%;
	height: calc(100% - var(--navBarHeight));
	background-image: linear-gradient(
		to bottom,
		white,
		rgba(118, 185, 71, .25)
	);
}
.editPreferences .bg img {
	position: absolute;
	top: 0; left: 0;

	width: 100%;
	height: 100%;
	object-fit: cover;
	filter: opacity(.075);
}

.editPreferences h1 {
	position: absolute;
	top: 6rem; left: 0rem;
	margin: 0;

	min-width: 31.5rem;
	width: 100%;
	text-align: center;
	font-size: 3rem;
	color: black;
}

.editPreferences .options {
	position: absolute;
	top: 12rem;

	width: 100%;
	display: flex;
	justify-content: center;
}

.editPreferences .avoid {
	position: relative;

	min-width: 12rem;
	width: 17rem;
	height: 25rem;

	display: flex;
	flex-direction: column;
}
.editPreferences .avoid h2 {
	margin: 0;
	margin-bottom: 1rem;

	font-size: 2rem;
	font-weight: 600;
}
.editPreferences .avoid div {
	margin-bottom: .5rem;
	display: flex;
	align-items: center;
	height: 2.5rem;
}
.editPreferences .avoid input {
	margin-left: .75rem;
	scale: 2.75;
	filter: contrast(0) brightness(3) drop-shadow(0 0 .02rem black);
}
.editPreferences .avoid input:checked {
	filter: hue-rotate(-136deg) brightness(1.475) saturate(.45) drop-shadow(0 0 .02rem black);
}
.editPreferences .avoid label {
	margin: 0;
	margin-left: 1rem;
	font-size: 1.5rem;
	font-weight: 500;
}

.editPreferences .less_more {
	position: relative;

	min-width: 26rem;
	width: 26rem;
	height: 30rem;
}
.editPreferences .less_more .less {
	position: absolute;
	top: 0; left: 0;

	width: 100%;
	height: 14rem;
}
.editPreferences .less_more .less h2 {
	margin: 0;
	margin-bottom: 1rem;

	font-size: 2rem;
	font-weight: 600;
}
.editPreferences .less_more .less div {
	margin-bottom: .5rem;
	display: flex;
	align-items: center;
	height: 2.5rem;
}
.editPreferences .less_more .less input {
	margin-left: .75rem;
	scale: 2.75;
	filter: contrast(0) brightness(3) drop-shadow(0 0 .02rem black);
}
.editPreferences .less_more .less input:checked {
	filter: hue-rotate(-136deg) brightness(1.475) saturate(.45) drop-shadow(0 0 .02rem black);
}
.editPreferences .less_more .less label {
	margin: 0;
	margin-left: 1rem;
	font-size: 1.5rem;
	font-weight: 500;
}
.editPreferences .less_more .more {
	position: absolute;
	top: 14rem; left: 0;

	width: 100%;
	height: 14rem;
}
.editPreferences .less_more .more h2 {
	margin: 0;
	margin-bottom: 1rem;

	font-size: 2rem;
	font-weight: 600;
}
.editPreferences .less_more .more div {
	margin-bottom: .5rem;
	display: flex;
	align-items: center;
	height: 2.5rem;
}
.editPreferences .less_more .more input {
	margin-left: .75rem;
	scale: 2.75;
	filter: contrast(0) brightness(3) drop-shadow(0 0 .02rem black);
}
.editPreferences .less_more .more input:checked {
	filter: hue-rotate(-136deg) brightness(1.475) saturate(.45) drop-shadow(0 0 .02rem black);
}
.editPreferences .less_more .more label {
	margin: 0;
	margin-left: 1rem;
	font-size: 1.5rem;
	font-weight: 500;
}

.editPreferences .save {
	position: absolute;
	top: 40rem; left: 0rem;
	width: 100%;

	display: flex;
	justify-content: center;
}
.editPreferences .save button {
	width: 22rem;
	height: 4.5rem;
	background-color: var(--color-main);
	border-radius: 1rem;
	border: none;

	color: white;
	font-size: 2rem;
	font-weight: 700;
	cursor: pointer;
}