@import url("https://fonts.googleapis.com/css?family=Lobster+Two:100,200,300,400,500,600,700");
@import url("https://fonts.googleapis.com/css?family=Fredericka+the+Great:100,200,300,400,500,600,700");


* {
    -webkit-user-drag: none;
}

:root {
    --navBarHeight: 3.5rem;
    --font-lobster-two: "Lobster Two", sans-serif;
    --font-fredericka-the-great: "Fredericka the Great", sans-serif;

    --color-main: rgba(118, 185, 71, 1);
    --color-darker: #2F5233;
}


/*#region Barriers*/
.barrier_1 {
    position: absolute;
    top: calc(var(--navBarHeight) + 26rem);
    left: 0rem;
    z-index: 1;

    width: 100%;
    height: 0.5rem;
    background-color: white;
    filter: drop-shadow(0 .5rem .5rem rgba(0, 0, 0, 0.25));
}
.barrier_2 {
    position: absolute;
    top: 72rem;
    left: 0rem;
    z-index: 1;

    width: 100%;
    height: 0.5rem;
    background-color: white;
    filter: drop-shadow(0 .5rem .5rem rgba(0, 0, 0, 0.25));
}
/*#endregion*/

/*#region SearchSection*/
.searchSection {
    position: absolute;
    top: var(--navBarHeight);

    width: 100%;
    height: 26rem;

    display: flex;
    justify-content: center;
}
.searchSection .bg {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;

    filter:
        brightness(.5)
        saturate(.9)
    ;
}
.searchSection h1 {
    position: absolute;
    top: 1.2rem;

    color: white;
    text-align: center;
    font-size: 4.5rem;
    font-weight: 400;
    font-family: var(--font-lobster-two);
}
.searchSection .searchBar {
    position: absolute;
    top: 18rem;

    min-width: 18rem;
    width: 35%;
    height: 3.3rem;
    border-radius: 10rem;

    background-color: rgba(255, 255, 255, 0.75);
    backdrop-filter: blur(.2rem);

    display: flex;
    align-items: center;
    overflow: hidden;
}
.searchSection .searchBar img {
    position: absolute;
    left: 1.2rem;

    width: 1.5rem;
    height: 1.5rem;
    filter: opacity(.75);
}
.searchSection .searchBar input {
    position: absolute;
    left: 3.5rem;

    width: calc(100% - 3rem - 2rem);
    height: 1.5rem;
    color: rgba(0, 0, 0, 0.75);
    font-size: 1.25rem;
    font-weight: 500;

    background-color: transparent;
    border: none;
    outline: none;
}
.searchSection .searchBar input::placeholder {
    color: rgba(0, 0, 0, 0.75);
}
/*#endregion*/

/*#region Trending & Deliveries*/
._trendingSection {
    position: absolute;
    top: 30rem;

    width: 100%;
    height: 42rem;
}
._trendingSection .bg {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;

    filter:
        brightness(.6)
        contrast(.5)
        saturate(1.3)
    ;
}
/* ._trendingSection .bg {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    background-color: #F1FFF4;
    overflow: hidden;
}
._trendingSection .bg .plate_1 {
    position: absolute;
    top: -16rem; left: -4rem;
    scale: 1;
    opacity: .4;
} */
._trendingSection .title {
    position: absolute;
    top: 3rem;
    
    width: 100%;
    display: flex;
    justify-content: center;
}
._trendingSection .title h1 {
    position: absolute;
    top: 0rem;

    margin: 0;
    user-select: none;
    font-family: var(--font-fredericka-the-great);
    font-weight: 500;
    font-size: 4rem;
    color: rgb(255, 255, 255);
    text-shadow: .1rem .1rem .5rem rgba(0, 0, 0, 0.75);
}
._trendingCards {
    position: absolute;
    top: 10rem;

    margin: 0;
    width: 100%;
}

._upcomingDeliveries {
    position: absolute;
    top: 26rem;
    left: 2rem;

    width: 100%;
}
._upcomingDeliveries h1 {
    position: absolute;
    user-select: none;
    font-size: 2rem;
    color: rgb(255, 255, 255);
    text-shadow: .1rem .1rem .5rem rgba(0, 0, 0, 0.75);
}
/*#endregion*/

/*#region ArticlesSection*/
.articlesSection {
    position: absolute;
    top: 72.5rem;
    left: 0rem;

    width: 100%;
    height: 90rem;

    display: flex;
    justify-content: center;
}
.articlesSection .topImg {
    position: absolute;
    top: 8rem;

    min-width: 30rem;
    width: 70%;
    height: 24rem;

    display: flex;
    justify-content: center;
}
.articlesSection .topImg .bg {
    position: absolute;
    top: 0; left: 0;

    border-radius: 1.5rem;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.articlesSection .topImg .bg img {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}
.articlesSection .topImg .title {
    position: absolute;
    top: -2rem;

    width: 12rem;
    height: 4rem;
    background-color: var(--color-main);
    filter: drop-shadow(0 .25rem .25rem rgba(0, 0, 0, 0.5));

    display: flex;
    justify-content: center;
}
.articlesSection .topImg .title h1 {
    position: absolute;
    top: .1rem;

    color: white;
    font-size: 1.5rem;
    font-weight: 700;
}

.articlesSection .desc {
    position: absolute;
    top: 34.5rem;
    
    min-width: 30rem;
    width: 70%;
    height: 24rem;

    display: flex;
}
.articlesSection .desc .left {
    position: relative;
    top: 0rem;

    min-width: 30rem;
    width: 65%;
}
.articlesSection .desc .left .text {
    position: absolute;
    top: 0rem;

    width: 57%;
}
.articlesSection .desc .left .text h1 {
    position: relative;
    top: .5rem; left: 0rem;
    margin: 0;

    color: black;
    font-weight: 700;
    font-size: 2rem;
}
.articlesSection .desc .left .text h2 {
    position: relative;
    top: 1rem; left: 0rem;
    margin: 0;

    color: black;
    font-weight: 600;
    font-size: 1rem;
}
.articlesSection .desc .left .text a:link {
    position: relative;
    top: 1rem; left: 0rem;
    margin: 0;

    color: var(--color-main);
    font-weight: 600;
    font-size: 1rem;
}
.articlesSection .desc .left .text a:visited {
    position: relative;
    top: 1rem; left: 0rem;
    margin: 0;

    color: var(--color-main);
    font-weight: 600;
    font-size: 1rem;
}
.articlesSection .desc .left .img {
    position: absolute;
    top: 0rem; right: 0rem;

    border-radius: 1.5rem;
    width: 40%;
    height: 24rem;
    /* border: .25rem solid white;
    box-sizing: border-box; */
    overflow: hidden;
}
.articlesSection .desc .left .img img {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}

.articlesSection .desc .right {
    position: relative;
    top: 0rem;
    left: 0rem;

    width: 35%;
    height: 24rem;
    overflow: hidden;
    text-decoration: none;
}
.articlesSection .desc .right .separator {
    position: absolute;
    top: 0rem;
    left: 1.5rem;

    width: .1rem;
    height: 24rem;
    border-radius: 1rem;
    background-color: black;
}
.articlesSection .desc .right .food_article {
    position: absolute;
    top: 0rem;
    left: 3rem;

    width: calc(100% - 3rem);
    height: 7.5rem;
    overflow: hidden;
}
.articlesSection .desc .right .food_article .img {
    position: absolute;
    top: 0; left: 0;

    max-width: 100%;
    width: 10rem;
    height: 100%;
    border-radius: 1rem;
    overflow: hidden;
}
.articlesSection .desc .right .food_article img {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}
.articlesSection .desc .right .food_article h2 {
    position: absolute;
    top: .25rem; left: 10.5rem;
    margin: 0;

    width: calc(100% - 10.5rem);
    color: black;
    font-size: 1rem;
    font-weight: 600;
}

.articlesSection .desc .right ._1 {
    top: 0rem;
}
.articlesSection .desc .right ._2 {
    top: 8.25rem;
}
.articlesSection .desc .right ._3 {
    top: 16.5rem;
}


.articlesSection .recommended_meals {
    position: absolute;
    top: 64rem;

    width: 100%;
}
.articlesSection .recommended_meals .title {
    position: absolute;
    top: 0rem; left: 0rem;
    width: 100%;
    display: flex;
    justify-content: center;
}
.articlesSection .recommended_meals .title h1 {
    margin: 0;
    font-size: 2.5rem;
    font-weight: 700;
    color: black;
}
.articlesSection .recommended_meals ._recommendedCards {
    position: absolute;
    top: 5.5rem;

    width: 100%;
}
/*#endregion*/

/*#region FamousSection*/
.famousSection {
    position: absolute;
    top: 162.5rem; left: 0rem;

    width: 100%;
    height: 23rem;

    background-color: var(--color-darker);
    display: flex;
    justify-content: center;
}
.famousSection h1 {
    position: absolute;
    top: 1rem;
    color: white;
    font-size: 2.25rem;
    font-weight: 700;
}

.famousSection .foods {
    position: absolute;
    bottom: 2.5rem;

    width: 50rem;
    height: 13.5rem;
}
.famousSection .food {
    position: absolute;
    top: 0rem;

    width: 10.5rem;
    height: 100%;

    display: flex;
    justify-content: center;
}
.famousSection .food div {
    position: absolute;
    top: 0rem;

    width: 10.5rem;
    height: 10.5rem;
    border-radius: 50rem;
    border: solid white .35rem;
    overflow: hidden;
}
.famousSection .food div img {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}
.famousSection .food h2 {
    position: absolute;
    top: 12rem;

    margin: 0;
    color: white;
    text-align: center;
    font-size: 1rem;
    font-weight: 600;
}

.famousSection ._1 {
    position: absolute;
    left: 0rem;
}
.famousSection ._2 {
    position: absolute;
    left: calc(10.5rem + 2.67rem);
}
.famousSection ._3 {
    position: absolute;
    right: calc(10.5rem + 2.67rem);
}
.famousSection ._4 {
    position: absolute;
    right: 0rem;
}
/*#endregion*/

/*#region Footer*/
.footer {
    position: absolute;
    top: 185.5rem; left: 0rem;

    width: 100%;
    height: 10rem;
    background-color: white;

    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .logos {
    width: 20rem;
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .logos div {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    width: 3rem;
    height: 3rem;
    border-radius: .5rem;
    overflow: hidden;
    cursor: pointer;
}
.footer .logos div img {
    width: 100%;
    height: 100%;
}
.footer .logos div .insta {
    scale: 1.15;
}
/*#endregion*/
