

:root {
	--height: 8rem;
	--descHeight: 3.5rem;
}

.upcDelCard {
	position: relative;
	min-width: 3rem;
	width: 11rem;
	height: var(--height);
	margin-right: 1rem;
	border-radius: 1rem;
	overflow: hidden;

    box-shadow: .1rem .1rem .5rem rgba(0, 0, 0, 0.15);
}

.upcDelCard .udImg {
	position: absolute;
    top: 0rem;
    left: 0rem;
    width: 100%;
    height: calc(var(--height) - var(--descHeight));
    object-fit: cover;
    object-position: top;
    user-select: none;
}
.upcDelCard .udBG {
	position: absolute;
    bottom: 0rem;
    left: 0rem;
    width: 100%;
    height: var(--descHeight);
    object-fit: cover;
    object-position: bottom;
    user-select: none;
}

.upcDelCard .nameNtime {
	position: absolute;
	bottom: 0rem;
	left: 0rem;

	width: 100%;
	height: var(--descHeight);
}

.upcDelCard .nntBG {
	position: absolute;
	bottom: 0rem;
	left: 0rem;

	width: 100%;
	height: 100%;

    background-image: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.35) 0%,
        rgba(255, 255, 255, 0.2) 10%,
        rgba(100, 100, 100, 0.15) 100%
    );
    backdrop-filter: brightness(.4) blur(.2rem);
}

.upcDelCard .name {
	position: absolute;
	top: .5rem;
	width: 100%;
	height: calc(100% - 1.75rem);
	display: flex;
	justify-content: center;
}
.upcDelCard .name h1 {
	margin: 0%;
	
	width: 90%;
	text-align: center;
	font-size: 1.1rem;
	font-weight: 600;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.upcDelCard .time {
	position: absolute;
	bottom: .6rem;
	width: 100%;
	height: 2rem;
	display: flex;
	justify-content: center;
}
.upcDelCard .time h1 {
	margin: 0%;
	
	text-align: center;
	bottom: 0rem;
	width: 90%;
	font-size: .75rem;
	font-weight: 500;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}